import { BaseYakuJudge } from './baseYakuJudge';
import { Hai } from '../models/hai';
import { Yaku } from '../models/yaku';
import { CombMentsu } from '../models/combMentsu';

/**
 *   染め手判定クラス
 */
export class SometeJudge extends BaseYakuJudge {
  private HONNITSU = new Yaku("混一色", "ホンイーソー", 4, true);
  private CHINITSU = new Yaku("清一色", "チンイーソー", 6, true);
  private TSU_I_SO = new Yaku("字一色", "ツーイーソー", 13, true);

  /**
   * 役判定
   * @param combMentsu 組み合わせメンツ
   * @return 役一覧
   */
  protected judgeYaku(combMentsu: CombMentsu): Array<Yaku> {
    let haiList = combMentsu.getHaiList();
    let typeSet = new Set<string>();
    // 牌種別を分ける
    haiList.forEach(hai => { 
      let type = hai.isJihai() ? "字牌" : hai.getType();
      if(typeSet.has(type) == false) {
        typeSet.add(type);
      }
    });
    let yakuList = new Array<Yaku>();
    switch (typeSet.size) {
      case 1:
        if (typeSet.has("字牌")) {
          yakuList.push(this.TSU_I_SO);
        } else {
          yakuList.push(this.CHINITSU);
        }
        break;
      case 2:
        if (typeSet.has("字牌")) {
          yakuList.push(this.HONNITSU);
        }
        break;
      default:
        break;
    }
    return yakuList;
  }
}


